import React from "react";
import { Button, Menu, Typography, Avatar, Space } from "antd";
import { Link } from 'react-router-dom';
import { HomeOutlined, MoneyCollectOutlined,  BuildOutlined, FundOutlined, MenuOutlined} from "@ant-design/icons";

import icon from '../img/cryptocurrency-logo.png';

const Navbar = () =>{
    return(
        <div className="nav-container">
            <div className="logo-container">
                <Avatar src={icon} size='large'/>
                <Typography.Title level={2} className='logo'>
                    <Link to='/'>Crypto</Link>
                </Typography.Title>
                {/* <Button className="menu-control-container">

                </Button> */}
            </div>
            <Menu theme='dark'>
                <Menu.Item icon={<HomeOutlined/>}>
                    <Link to='/'>Home</Link>
                </Menu.Item>
                <Menu.Item icon={<FundOutlined/>}>
                    <Link to='/cryptocurrency'>Cryptocurrency</Link>
                </Menu.Item>
                <Menu.Item icon={< MoneyCollectOutlined/>}>
                    <Link to='/exchanges'>Exchanges</Link>
                </Menu.Item>
                <Menu.Item icon={<BuildOutlined/>}>
                    <Link to='/news'>News</Link>
                </Menu.Item>
            </Menu>
         
        </div>
    )
}

export default Navbar;